export default function Employee(props) {
    const classes = useStyles()

    var result = Object.keys(props.selectedEmployee).map((key) => {
        return [key, ": ", props.selectedEmployee[key]];
    });

    // console.log("employee on employee: ",result)

    return (
        <div>
            <List>
                {result.map((item, index) => (
                    <div><ListItem dense divider button key={item}>

                        <ListItemText>
                            <Typography variant="caption">{item[0] + ":"}</Typography>
                        </ListItemText>

                        <Typography variant="caption">{item[2]}</Typography>

                    </ListItem>
                        <Divider style={{ backgroundColor: "grey", padding: "1px", margin: "5px" }} orientation="horizontal" />
                    </div>

                ))}
            </List>
        </div>

    )
}