import React, { useEffect } from 'react';
import { Button, TextField, InputAdornment, Input, ListItem, List, ListItemText, Typography, Divider } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles(theme => ({
    dialogOpenButton: {
        color: "black",
        //backgroundColor: "#b71c1c",
        border: "2px solid grey"

    },
    voteTextField: {

    },
    dialog: {
        border: "3px solid grey"

    },
    okButton: {
        color: "black"
    },
    cancelButton: {
        color: "black"
    },

}));


export default function AlertDialogSlide(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    //console.log("props.employee::::::", props.employee)
    //const gradeObjFromServer = props.employee.objectives[0].objectiveList[props.index].graderList.filter(i => i.id === props.user.id)[0]

    var gradeObjFromServer = ""
    if (props.employee.objectives[0].objectiveList[props.index].graderList !== undefined & props.employee.objectives[0].objectiveList[props.index].graderList !== ""){
        gradeObjFromServer = props.employee.objectives[0].objectiveList[props.index].graderList.filter(i => i.id === props.user.id)[0]
    }

    console.log("emplyee from wtch dialog: ", props.employee.objectives[0].objectiveList[props.index].graderList)

    return (
        <div>
            <Button variant="outlined" size="small" onClick={handleClickOpen} className={classes.dialogOpenButton}>
                İZLE
                {console.log(gradeObjFromServer !== undefined ? gradeObjFromServer.grade : "gradeFromServer undefined")}
            </Button>

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
                //className={classes.dialog}
                fullWidth
            >
                <div className={classes.dialog}>
                    <DialogTitle id="alert-dialog-slide-title">{props.employee.name + " " + props.employee.surname}</DialogTitle>
                    <DialogContent dividers>
                        <DialogContentText id="alert-dialog-slide-description">

                            <Typography component="span" variant="caption">
                                Hedef Açıklaması: {props.employee.objectives[0].objectiveList[props.index].description}
                            </Typography>
                            <br/>
                             
                            <Typography component="span" variant="caption">
                                Hedef Çeşidi: {props.employee.objectives[0].objectiveList[props.index].objectiveKind}
                            </Typography>
                            <br/>

                            <Typography component="span" variant="caption">
                                Periyot: {props.employee.objectives[0].objectiveList[props.index].period}
                            </Typography>
                            <br/>
                            
                            <Typography component="span" variant="caption">
                                Hedef Değer: {props.employee.objectives[0].objectiveList[props.index].target}
                            </Typography>
                            <br/>
                             
                            <Typography component="span" variant="caption">
                                Ağırlık: {props.employee.objectives[0].objectiveList[props.index].credit}
                            </Typography>

                            <Typography component="span" variant="caption">
                                Birim: {props.employee.objectives[0].objectiveList[props.index].unit}
                            </Typography>
                            <br/>

                        </DialogContentText>

                        <Typography align="center">Puan Verenler</Typography>
                        <Divider style={{ backgroundColor: "black", color: "black", padding: "1px", margin: "5px" }} orientation="horizontal" />
                        
                        { props.employee.objectives[0].objectiveList[props.index].graderList !== "" & props.employee.objectives[0].objectiveList[props.index].graderList !== undefined
                        ?
                        <List>
                            {props.employee.objectives[0].objectiveList[props.index].graderList.map((item, index) => (
                                <div>
                                    <ListItem dense divider button key={item}>
                                        <ListItemText>
                                            <Typography variant="caption">{item.name + " "}</Typography>
                                            <Typography variant="caption">{item.surname + ":"}</Typography>
                                        </ListItemText>

                                        <Typography variant="caption">{item.grade}</Typography>

                                    </ListItem>

                                    <Divider style={{ backgroundColor: "black", color: "black", padding: "1px", margin: "5px" }} component="li" orientation="horizontal" />
                                </div>
                            ))}
                        </List>
                        :
                        ""
                        }

                    </DialogContent>


                    <DialogActions>
                        <Button onClick={handleClose} variant="contained" className={classes.okButton} >
                            ÇIKIŞ
                    </Button>

                    </DialogActions>

                </div>
            </Dialog>
        </div>
    );
}
