export default function ObjectivesTableWatch(props) {
    const classes = useStyles();
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = event => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    console.log("props.employee: ", props.employee)

    const rows = []

    const mafon = () => {
        if (props.employee !== "") {
            props.employee.objectives.map((i, index0) => i.objectiveList.map((item, index1) => {
                rows.push(createData(i.year, props.employee.id, props.employee.name + " " + props.employee.surname,
                    props.employee.department, item.objectiveType, item.objectiveKind, item.description,
                    item.period, item.credit, item.target, item.unit, item.lowerLimit, item.upperLimit))
            })
            )
        }
    }
    mafon()

    return (
        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table" size="small">
                    <TableHead>
                        <TableRow>
                            {columns.map(column => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth, backgroundColor: "#b71c1c", color: "white" }}
                                    padding="checkbox"
                                >
                                    <Typography variant="subtitle2">{column.label}</Typography>
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody >
                        {rows !== null ?
                            rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
                                return (
                                    <TableRow role="checkbox" tabIndex={-1} key={index} className={classes.tableBodyRows}>
                                        {columns.map(column => {
                                            const value = row[column.id];
                                            return (
                                                column.id === 'watch'
                                                    ?
                                                    <TableCell>
                                                        <WatchDialog index={index} employee={props.employee} user={props.user} />
                                                    </TableCell>
                                                    :
                                                    <TableCell key={column.id} align={column.align} style={{ height: "" }}>
                                                        <Typography variant="caption">{column.format && typeof value === 'number' ? column.format(value) : value}</Typography>
                                                    </TableCell>
                                            );
                                        })}

                                    </TableRow>
                                );
                            }) : null}
                    </TableBody>
                </Table>
            </TableContainer>

            <TablePagination style={{ color: "black"}}
                rowsPerPageOptions={[5, 10, 25, 100]}
                component="div"
                count={rows !== null ? rows.length : 0}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                labelRowsPerPage=""
                className={classes.tablePagination}
            //ActionsComponent="da"
            >
            </TablePagination>

            {console.log("rows.length", rows.length)}

        </Paper>
    );
}
