import React, { useEffect } from "react"
import {
    Grid, Paper, IconButton, Box, Divider, Button, Menu, TextField, FormControl,
    InputAdornment, Fab, OutlinedInput, FormHelperText, ButtonGroup,
    Typography, MenuItem, Input
} from '@material-ui/core'
import SubdirectoryArrowLeftIcon from '@material-ui/icons/SubdirectoryArrowLeft';
import SaveIcon from '@material-ui/icons/Save';
import ClearIcon from '@material-ui/icons/Clear';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import { Alert, AlertTitle } from '@material-ui/lab';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(2),
            //width: 400,
        },
        height: "",

    },
    topGrid: {
        //margin: theme.spacing(2),
        width: "100%",
        marginTop: "0%",
        //height: "100%",
    },
    editButtons: {
        color: "grey",
        '&:hover': {
            color: "grey",
            //backgroundColor: "white"
        }
    }
}));

const yearEnum = Object.freeze({
    _2020: 2020,
    //_2021: 2021
})
const typeEnum = Object.freeze({
    is: "İş Hedefleri",
    kisisel: "Kişisel Hedefler"
})
const kindEnum = Object.freeze({
    is: "İş Performansı",
    hata: "Hata Oranı",
    devam: "Devamsızlık Performansı",
    verim: "Verimlilik Performansı",
    maliyet: "Maliyet Azaltma",
    surec: "Süreç Etkinliğini Artırma Performansı",
    iyiles: "İyileştirme Önerileri",
    teslim: "Teslim Etme Performansı",
    yardim: "Yardımcı Sanayiye İş Aktarma Performansı"
})
const periodEnum = Object.freeze({
    gun: "Günlük",
    ay: "Aylık",
    ucay: "3 Aylık",
    altiay: "6 Aylık",
    dokuzay: "9 Aylık",
    yil: "Yıllık",
})
const unitEnum = Object.freeze({
    yuzde: "Yüzde",
    adam: "Adam/Saat",
    adet: "Adet",
    ay: "Ay",
    bulgu: "Bulgu",
    dakika: "Dakika",
    gun: "Gün",
    hafta: "Hafta",
    kere: "Kere/Kez",
    saat: "Saat",
    tl: "TL",
    puan: "Puan"
})

export default function EditObjectivesForm(props) {
    const classes = useStyles();

    const [objTypeInput, setObjTypeInput] = React.useState("")
    const [objKindInput, setObjKindInput] = React.useState("")
    const [descriptionInput, setDescriptionInput] = React.useState("")
    const [periodInput, setPeriodInput] = React.useState("")
    const [unitInput, setUnitInput] = React.useState("")
    const [creditInput, setCreditInput] = React.useState("")
    const [targetInput, setTargetInput] = React.useState("")
    const [minInput, setMinInput] = React.useState("")
    const [maxInput, setMaxInput] = React.useState("")

    useEffect(() => {
        if (props.selectedObjective !== "" & props.selectedObjective !== null & props.selectedObjective !== undefined) {
            setObjTypeInput(props.selectedObjective.objective.objectiveType)
            setObjKindInput(props.selectedObjective.objective.objectiveKind)
            setDescriptionInput(props.selectedObjective.objective.description)
            setPeriodInput(props.selectedObjective.objective.period)
            setUnitInput(props.selectedObjective.objective.unit)
            setCreditInput(props.selectedObjective.objective.credit)
            setTargetInput(props.selectedObjective.objective.target)
            setMinInput(props.selectedObjective.objective.lowerLimit)
            setMaxInput(props.selectedObjective.objective.upperLimit)
        }/* else{
            setObjTypeInput("")
            setObjKindInput("")
            setDescriptionInput("")
            setPeriodInput("")
            setUnitInput("")
            setCreditInput("")
            setTargetInput("")
            setMinInput("")
            setMaxInput("")
        } */
    }, [props.selectedObjective])

    const updatedObjective = {
        creatorID: "",
        objectiveType: objTypeInput,
        objectiveKind: objKindInput,
        description: descriptionInput,
        period: periodInput,
        credit: creditInput,
        target: targetInput,
        unit: unitInput,
        lowerLimit: minInput,
        upperLimit: maxInput
    }

    const handleUpdateObjButton = () => {
        props.updateObjective(updatedObjective);
    }

    //console.log("selectedObjective: ", props.selectedObjective)

    const handleObjTypeInput = (event) => {
        setObjTypeInput(event.target.value)
    }

    const handleObjKindInput = (event) => {
        setObjKindInput(event.target.value)
    }

    const handleDescriptionChange = (event) => {
        setDescriptionInput(event.target.value)
    }

    const handlePeriodInput = (event) => {
        setPeriodInput(event.target.value)
    }

    const handleUnitInput = (event) => {
        setUnitInput(event.target.value)
    }

    const handleCreditInput = (event) => {
        setCreditInput(event.target.value)
    }

    const handleTargetInput = (event) => {
        setTargetInput(event.target.value)
    }

    const handleMinInput = (event) => {
        setMinInput(event.target.value)
    }

    const handleMaxInput = (event) => {
        setMaxInput(event.target.value)
    }

    const handleClearDescriptionButton = () => {
        setDescriptionInput("")
    }

    return (

        <Paper elevation={9} square={true} style={{ display: "", height: "100%", width: "100%", border: "0px solid black", margin: "0px", backgroundColor: "#e0e0e0" }}>
            <FormControl className={classes.root} noValidate autoComplete="off">

                <Grid container item xs={12} spacing={0} className={classes.topGrid} >

                    <Grid container item xs={12} spacing={0} direction="column" style={{ minHeight: "", backgroundColor: "#b71c1c" }}>
                        <div style={{ minHeight: "" }}>
                            <IconButton aria-label="delete" onClick={props.handleCloseEditButton} style={{ float: "left" }} size="small">
                                <ArrowForwardIosIcon fontSize="default" color="primary" style={{ color: "white" }} />
                            </IconButton>
                        </div>
                    </Grid>

                    {/* <Grid container item xs={12} spacing={0} direction="column" style={{ height: "70vh", borderLeft: "0px solid black", borderRight: "0px solid black" }}> */}
                    <Grid style={{ backgroundColor: "#e0e0e0", height: "35vh", width: "100%", border: "0px solid black", overflow: "auto" }}>

                        <TextField
                            required
                            id="outlined-required"
                            label="Hedef Türü"
                            value={objTypeInput}
                            //defaultValue="select"
                            variant="outlined"
                            size="small"
                            select
                            onChange={handleObjTypeInput}
                            style={{ width: "40%" }}
                        >
                            <MenuItem value="" >
                                <em>None</em>
                            </MenuItem>
                            {/* <MenuItem key="type" value={typeEnum}>
                                {typeEnum}
                            </MenuItem> */}
                            {Object.keys(typeEnum).map((item, index) =>
                                <MenuItem key={"type" + index} value={typeEnum[item]}>
                                    {typeEnum[item]}
                                </MenuItem>
                            )}
                        </TextField>

                        <TextField
                            required
                            id="outlined-required"
                            label="Hedef Çeşidi"
                            value={objKindInput}
                            //defaultValue="select"
                            variant="outlined"
                            size="small"
                            select
                            onChange={handleObjKindInput}
                            style={{ width: "40%" }}
                        >
                            <MenuItem value="" >
                                <em>None</em>
                            </MenuItem>
                            {/* <MenuItem key="kind" value={kindEnum}>
                                {kindEnum}
                            </MenuItem> */}
                            {Object.keys(kindEnum).map((item, index) =>
                                <MenuItem key={"kind" + index} value={kindEnum[item]}>
                                    {kindEnum[item]}
                                </MenuItem>
                            )}
                        </TextField>

                        <TextField
                            required
                            id="outlined-required"
                            label="Dönem"
                            value={periodInput}
                            //defaultValue="select"
                            variant="outlined"
                            size="small"
                            select
                            onChange={handlePeriodInput}
                            style={{ width: "40%" }}
                        >
                            <MenuItem value="" >
                                <em>None</em>
                            </MenuItem>
                            {/* <MenuItem key="period" value={periodEnum}>
                                {periodEnum}
                            </MenuItem> */}
                            {Object.keys(periodEnum).map((item, index) =>
                                <MenuItem key={"period" + index} value={periodEnum[item]}>
                                    {periodEnum[item]}
                                </MenuItem>
                            )}
                        </TextField>

                        <TextField
                            required
                            id="outlined-required"
                            label="Birim"
                            //defaultValue="select"
                            value={unitInput}
                            variant="outlined"
                            size="small"
                            select
                            onChange={handleUnitInput}
                            style={{ width: "40%" }}
                        >
                            <MenuItem value="" >
                                <em>None</em>
                            </MenuItem>
                            {/* <MenuItem key="unit" value={unitEnum}>
                                {unitEnum}
                            </MenuItem> */}
                            {Object.keys(unitEnum).map((item, index) =>
                                <MenuItem key={"unit" + index} value={unitEnum[item]}>
                                    {unitEnum[item]}
                                </MenuItem>
                            )}
                        </TextField>

                        <TextField
                            label="Ağırlık"
                            id="outlined-start-adornment"
                            //className={clsx(classes.margin, classes.textField)}
                            /* InputProps={{
                                startAdornment: <InputAdornment marginDense position="start">%</InputAdornment>,
                            }} */
                            variant="outlined"
                            type="number"
                            size="small"
                            value={creditInput}
                            onChange={handleCreditInput}
                            style={{ width: "40%" }}
                        />
                        <TextField
                            required
                            id="outlined-required"
                            label="Hedef Değer"
                            value={targetInput}
                            onChange={handleTargetInput}
                            //defaultValue=""
                            type="number"
                            variant="outlined"
                            size="small"
                            style={{ width: "40%" }}
                        />

                        <TextField
                            required
                            id="outlined-required"
                            label="Alt Limit"
                            value={minInput}
                            onChange={handleMinInput}
                            //defaultValue=""
                            type="number"
                            variant="outlined"
                            size="small"
                            style={{ width: "40%" }}
                        />
                        <TextField
                            required
                            id="outlined-required"
                            label="Üst Limit"
                            value={maxInput}
                            onChange={handleMaxInput}
                            //defaultValue=""
                            type="number"
                            variant="outlined"
                            size="small"
                            style={{ width: "40%", border: "0px solid black" }}
                        />

                    </Grid>

                    <Grid style={{ height: "35vh", width: "100%", backgroundColor: "#e0e0e0", border: "0px solid black", overflow: "auto" }}>
                        <TextField
                            required
                            id="outlined-required"
                            label="Açıklama"
                            onChange={handleDescriptionChange}
                            value={descriptionInput}
                            //defaultValue=""
                            variant="filled"
                            multiline
                            rows="12"
                            //style={{ height: "100%", width:"90%" }}
                            style={{ width: "80%", height: "", border: "0px solid black" }}
                            margin="normal"
                        //size="medium"
                        //rowsMax="10"

                        ></TextField>
                    </Grid>
                    {/* </Grid> */}

                    <Grid container item xs={12} direction="column" style={{ minHeight: "", width: "" }}>

                        <div className={classes.root1} style={{ height: "", backgroundColor: "" }}>
                            <ButtonGroup variant="text" size="large" color="primary" aria-label="text primary button group">
                                <Button
                                    startIcon={<SaveIcon />}
                                    onClick={handleUpdateObjButton}
                                    className={classes.editButtons}
                                >
                                    Kaydet
                                </Button>
                                <Button
                                    startIcon={<ClearIcon />}
                                    onClick={props.handleCloseEditButton}
                                    className={classes.editButtons}>
                                    İptal
                                        </Button>
                            </ButtonGroup>
                        </div>
                    </Grid>
                </Grid>


            </FormControl>

        </Paper>
    )
}