export default function MiniDrawer(props) {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(!open);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    //dashboard
    const [isOpenDashboard, setIsOpenDashboard] = React.useState(true)

    //vote
    const [isOpenVoteObjectives, setIsOpenVoteObjectives] = React.useState(false)
    const [isOpenWatchObjectives, setIsOpenWatchObjectives] = React.useState(false)

    //edit
    const [isOpenSetObjectives, setIsOpenSetObjectives] = React.useState(false)
    const [isOpenSetUsers, setIsOpenSetUsers] = React.useState(false)

    //users
    const [isOpenProfile, setIsOpenProfile] = React.useState(false)
    const [isOpenUsers, setIsOpenUsers] = React.useState(false)

    //settings
    const [isOpenSettings, setIsOpenSettings] = React.useState(false)

    //view state
    const [prevView, setPrevView] = React.useState([setIsOpenDashboard])

    const handleView = (newView) => {
        prevView[0](false)
        newView(true)
        setPrevView([newView])
    }

    const renderDashboard = () => {
        if (isOpenDashboard) {
            return <Dashboard />
        }
    }

    const renderVoteObjectives = () => {
        if (isOpenVoteObjectives) {
            return <VoteContainer user={props.user} />
        }
    }

    const renderWatchObjectives = () => {
        if (isOpenWatchObjectives) {
            return <WatchContainer user={props.user} />
        }

    }

    const renderSetObjectives = () => {
        if (isOpenSetObjectives) {
            return <SetObjectivesContainer user={props.user}/>
        }
    }

    const renderSetUsers = () => {
        if (isOpenSetUsers) {
            return <SetUsersContainer />
        }
    }

    const renderProfile = () => {
        if (isOpenProfile) {
            return <ProfileContainer />
        }
    }

    const renderUsers = () => {
        if (isOpenUsers) {
            return <UsersContainer />
        }
    }

    const renderSettings = () => {
        if (isOpenSettings) {
            return <SettingsContainer />

        }
    }

    return (
        <div className={classes.root}>
            <CssBaseline />
            {/* <Bar handleDrawerOpen={handleDrawerOpen} open={open} /> */}

            <AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                 <Toolbar1 handleDrawerOpen={handleDrawerOpen} open={open} user={props.user}/>
            </AppBar>

            <Drawer
                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
            >
                
                {open ?
                    <Paper style={{ height: "", color: "black" }} className={classes.papers}>
                        <img src={logo} className="App-logo" alt="logo" />
                        {/* <div className={classes.toolbar}>
                    <IconButton onClick={handleDrawerClose}>
                        {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                    </IconButton>
                </div> */}
                    </Paper>
                    :
                    ""
                }
                {!open ? <div className={classes.toolbar}></div> : ""}
                {open ? <Divider style={{ border: "0px solid #b71c1c" }} /> : ""}
                <Paper className={classes.papers} >
                    <List dense>
                        <ListItem button selected={isOpenDashboard} key="Ana Sayfa" onClick={() => handleView(setIsOpenDashboard)}>
                            <ListItemIcon ><DashboardIcon className={isOpenDashboard ? classes.selected : classes.notSelected} fontSize="default" /> </ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Ana Sayfa</Typography>} />
                        </ListItem>
                    </List>
                </Paper>
                <Divider style={{ border: "0px solid #b71c1c" }} />
                <Paper className={classes.papers} >
                    <List dense>
                        <ListItem button selected={isOpenVoteObjectives} key="Hedefler" onClick={() => handleView(setIsOpenVoteObjectives)} >
                            <ListItemIcon ><ThumbUpAltIcon className={isOpenVoteObjectives ? classes.selected : classes.notSelected} fontSize={isOpenVoteObjectives ? "default" : "default"} /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Puan Ver</Typography>} />
                        </ListItem>
                    </List>
                </Paper>
                <Divider style={{ border: "0px solid #b71c1c" }} />
                <Paper className={classes.papers} >
                    <List dense>
                        <ListItem button selected={isOpenWatchObjectives} key="İzle" onClick={() => handleView(setIsOpenWatchObjectives)}>
                            <ListItemIcon ><VisibilityIcon className={isOpenWatchObjectives ? classes.selected : classes.notSelected} fontSize={isOpenWatchObjectives ? "default" : "default"} /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">İzle</Typography>} />
                        </ListItem>
                    </List>
                </Paper>
                <Divider style={{ border: "0px solid #b71c1c" }} />
                <Paper className={classes.papers} >
                    <List dense>
                        <ListItem button selected={isOpenSetObjectives} key="İzle" onClick={() => handleView(setIsOpenSetObjectives)}>
                            <ListItemIcon ><AssignmentIcon className={isOpenSetObjectives ? classes.selected : classes.notSelected} fontSize="default" /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Hedef Düzenle</Typography>} />
                        </ListItem>

                        {/* <ListItem button selected={isOpenSetUsers} key="Edit" onClick={() => handleView(setIsOpenSetUsers)}>
                            <ListItemIcon ><GroupAddIcon className={isOpenSetUsers ? classes.selected : classes.notSelected} fontSize="default" /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Kullanıcı Düzenle</Typography>} />
                        </ListItem> */}
                    </List>
                </Paper>
                <Divider style={{ border: "0px solid #b71c1c" }} />
                {/* <Paper className={classes.papers} >
                    <List dense>
                        <ListItem disabled button selected={isOpenProfile} key="profile" onClick={() => handleView(setIsOpenProfile)}>
                            <ListItemIcon><PersonIcon className={isOpenProfile ? classes.selected : classes.notSelected} fontSize="default" /> </ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Profil</Typography>} />
                        </ListItem>
                        <ListItem disabled button selected={isOpenUsers} key="user" onClick={() => handleView(setIsOpenUsers)}>
                            <ListItemIcon><GroupIcon className={isOpenUsers ? classes.selected : classes.notSelected} fontSize="default" /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Personel</Typography>} />
                        </ListItem>

                    </List>
                </Paper> */}
                <Divider style={{ border: "0px solid #b71c1c" }} />
                <Paper className={classes.papers} style={{ marginBottom: 0 }}>
                    <List dense>
                        {/* <ListItem disabled button selected={isOpenSettings} key="Ayarlar" onClick={() => handleView(setIsOpenSettings)}>
                            <ListItemIcon><SettingsIcon className={isOpenSettings ? classes.selected : classes.notSelected} fontSize="default" /> </ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Ayarlar</Typography>} />
                        </ListItem> */}

                        <ListItem button selected={false} onClick={props.handleLogout} >
                            <ListItemIcon><PowerOffIcon className={false ? classes.selected : classes.notSelected} fontSize="default" /></ListItemIcon>
                            <ListItemText primary={<Typography variant="caption">Çıkış Yap</Typography>} />
                        </ListItem>

                    </List>
                </Paper>
                <Divider style={{ border: "0px solid #b71c1c" }} />
                <Paper style={{ height: "100%", marginBottom: 0 }} className={classes.papers}>
                    {/* <ListItem button onClick={props.handleLogout} style={{}}>
                        <ListItemIcon> <PowerOffIcon /></ListItemIcon >
                        <ListItemText primary="Logout" />
                    </ListItem> */}
                </Paper>
            </Drawer>
            <main className={classes.content}>
                <div className={classes.toolbar} />
                {renderDashboard()}
                {renderVoteObjectives()}
                {renderWatchObjectives()}
                {renderSetObjectives()}
                {renderSetUsers()}
                {renderProfile()}
                {renderUsers()}
                {renderSettings()}


            </main>
        </div>
    );
}
