import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Paper } from '@material-ui/core';
import axios from 'axios';

import EmployeeSelectListWatch from "./employeeSelectListWatch"
import ObjectivesTableWatch from "./objectivesTableWatch"

import mdata from "../common/mockdata"


const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
    },


}));

export default function Watch(props) {
    const classes = useStyles()

    const [selectedEmployee, setSelectedEmployee] = React.useState('');
    const [employee, setEmployee] = React.useState('')
    // console.log("employee: ", employee)

    const handleSelectEmployee = (event) => {
        setSelectedEmployee(event.target.value);
        getEmployeeInfo(event.target.value.id)
        //console.log("onchangee", event.target.value)
    };

    /* const getEmployeeInfo = (id) => {
        axios.get(`http://172.25.10.12:8081/user/findUserById?userID=` + id)
            .then(res => {
                if (res.status === 200) {
                    // console.log("res.data", res.data) 
                    setEmployee(res.data)
                }
                //this.setState({ persons });
            })
    } */

    const getEmployeeInfo = (id) => {
        console.log("handle selecten get emp id: ", id)
        if (id !== undefined) {
            mdata.map((item, index) => {
                if (item.id === id) {
                    setEmployee(item)
                }
            })
        }else{
            setEmployee("")
        }
    }

    return (

        <Grid container item xs={12} spacing={2} style={{ height: "85vh" }}>

            <Grid container item xs={3} direction="column" style={{ height: "15%" }}>
                <Paper style={{ height: "100%", margin: "0px" }}>
                    <EmployeeSelectListWatch handleSelectEmployee={handleSelectEmployee} selectedEmployee={selectedEmployee} user={props.user} />
                </Paper>
            </Grid>

            <Grid container item xs={9} direction="column" style={{ height: "15%" }}>
                <Paper style={{ height: "100%", margin: "0px" }}>

                </Paper>
            </Grid>


            <Grid container item xs={12} direction="column" style={{ height: "85%" }}>
                <div style={{ height: "100%" }}>
                    <ObjectivesTableWatch employee={employee} user={props.user} />
                </div>

                {/* <Paper style={{ height: "100%" }}>
                        
                    </Paper> */}

            </Grid>

        </Grid>


    )
}

