function createData(year, id, nameSurname, targetSection,
    targetType, targetSort, description, period, weight,
    targetValue, unit, minLimit, maxLimit) {
    //const density = population / size;
    return {
        year, id, nameSurname, targetSection,
        targetType, targetSort, description, period, weight,
        targetValue, unit, minLimit, maxLimit
    };
}

