import React from 'react';
//import logo from './logo-tr.png';
import './App.css';

import SignIn2 from "./components/login/login2.js"
import axios from 'axios';

import Drawer2 from "./components/layout/drawer2"

function App() {

  //localStorage.clear();
  if (localStorage.getItem("myToken") !== null & localStorage.getItem("myToken") !== undefined) {
    axios.defaults.headers.common["authorization"] = "Bearer " + localStorage.getItem("myToken")
  }
  const [user, setUser] = React.useState(JSON.parse(localStorage.getItem("fark")));

  const checkAutanticated = (userObj) => {
    if (userObj !== null & userObj !== undefined) {
      setUser(userObj)
    }
  }

  console.log("user: ", user)

  const handleLogout = () => {
    console.log("handling logout")
    localStorage.clear();
    setUser(null)
  }

  return (
    <div className="App">
      {user === null ? <SignIn2 checkAutanticated={checkAutanticated} />
        :
        <Drawer2 user={user} handleLogout={handleLogout} />}
      {/* <Drawer user={user} /> */}
      {/* <SignIn2 checkAutanticated={checkAutanticated}/> */}
    </div>
  );
}

export default App;
