import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import { MenuItem, Typography } from '@material-ui/core';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5',
  },
})(props => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));

const StyledMenuItem = withStyles(theme => ({
  root: {
    '&:focus': {
      //backgroundColor: theme.palette.primary.main,
      backgroundColor: "black",
      color: "white",
      '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
        color: theme.palette.common.white,
      },
    },
  },
}))(MenuItem);

export default function SimpleMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  console.log("?????? menu graders: ", props.graderList)
  return (
    <div>
      <Button
        aria-controls="simple-menu"
        aria-haspopup="true"
        onClick={handleClick}
        endIcon={<KeyboardArrowDownIcon />}
      >
        <Typography align="right" variant="caption">Puan Verenler</Typography>
      </Button>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {props.graderList !== undefined & props.graderList !== "" ?
          [props.graderList.length !== 0 ?
            props.graderList.map((item, index) => (
              <StyledMenuItem button={true} onClick={handleClose} key={index}>
                <Typography variant="caption">
                  {item.name + " " + item.surname + ": " + item.grade}
                </Typography>
              </StyledMenuItem>

            ))
            :
            <MenuItem onClick={handleClose}>
              <Typography variant="caption">
                Puan Bulunamadı
                            </Typography>
            </MenuItem>
          ] : <MenuItem onClick={handleClose}>
            <Typography variant="caption">
              Puan Bulunamadı
                    </Typography>
          </MenuItem>
        }
      </StyledMenu>
    </div>
  );
}
