import React, { useEffect } from 'react';
import { Button, TextField, InputAdornment, Input, Typography, Grid, Paper } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { makeStyles } from '@material-ui/core/styles';

import VoteGradersMenu from './voteGradersMenu'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles(theme => ({
    dialogOpenButton: {
        color: "black",
        //backgroundColor: "#b71c1c",
        border: "2px solid grey"

    },
    voteTextField: {

    },
    dialog: {
        border: "2px solid black"

    },
    okButton: {
        color: "black"
    },
    cancelButton: {
        color: "black"
    },

}));

export default function AlertDialogSlide(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    //const [grade, setGrade] = React.useState("");
    const [isValidInput, setIsValidInput] = React.useState(false);
    const [inputGrade, setInputGrade] = React.useState("");

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleOkButton = () => {
        //setGrade(inputGrade)       
        props.handleVoteClick(props.index, inputGrade)
        setOpen(false);
        setInputGrade("");
        setIsValidInput(false)

    };

    const handleClose = () => {
        //setGrade("");      
        setInputGrade("")
        setOpen(false);
    };
    //console.log("inputgrade: ", inputGrade)
    const handleVoteChange = (event) => {
        setInputGrade(event.target.value)
        if (event.target.value >= 0 & event.target.value <= 100) {
            setIsValidInput(true)
        } else {
            setIsValidInput(false)
        }
    }

    
    var gradeObjFromServer = ""
    if (props.employee.objectives[0].objectiveList[props.index].graderList !== undefined & props.employee.objectives[0].objectiveList[props.index].graderList !== ""){
        gradeObjFromServer = props.employee.objectives[0].objectiveList[props.index].graderList.filter(i => i.id === props.user.id)[0]
    }
    console.log("props.employee from vote:", props.employee)

    return (
        <div>
            <Button variant="outlined" size="small" onClick={handleClickOpen} className={classes.dialogOpenButton}>
                Puanla {gradeObjFromServer !== undefined ? gradeObjFromServer.grade : "-"}
                {console.log(gradeObjFromServer !== undefined ? gradeObjFromServer.grade : "gradeFromServer undefined")}
            </Button>

            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
                //className={classes.dialog}
                fullWidth
                transitionDuration={300}
                
            >
                <div className={classes.dialog}>
                    <Grid container item xs={12}>
                        <Grid container item xs={6} direction="column">

                            <DialogTitle id="alert-dialog-slide-title">Önceki puanınız: {gradeObjFromServer !== undefined ? gradeObjFromServer.grade : "-"}</DialogTitle>

                        </Grid>
                        <Grid container item xs={6} direction="column">

                            <DialogTitle>
                                <Typography align="right" variant="caption">

                                <VoteGradersMenu graderList={props.employee.objectives[0].objectiveList[props.index].graderList}/>
                            </Typography>

                            </DialogTitle>

                        </Grid>
                    </Grid>
                    <DialogContent dividers>

                        <DialogContentText id="alert-dialog-slide-description">



                            <Typography component="span" variant="caption">
                                Hedef Açıklaması: {props.employee.objectives[0].objectiveList[props.index].description}
                            </Typography>
                            <br />

                            <Typography component="span" variant="caption">
                                Hedef Çeşidi: {props.employee.objectives[0].objectiveList[props.index].objectiveKind}
                            </Typography>
                            <br />

                            <Typography component="span" variant="caption">
                                Periyot: {props.employee.objectives[0].objectiveList[props.index].period}
                            </Typography>
                            <br />

                            <Typography component="span" variant="caption">
                                Hedef Değer: {props.employee.objectives[0].objectiveList[props.index].target}
                            </Typography>
                            <br />

                            <Typography component="span" variant="caption">
                                Ağırlık: {props.employee.objectives[0].objectiveList[props.index].credit}
                            </Typography>
                            <br />

                            <Typography component="span" variant="caption">
                                Birim: {props.employee.objectives[0].objectiveList[props.index].unit}
                            </Typography>


                        </DialogContentText>

                        <TextField
                            autoFocus
                            error={!isValidInput}
                            margin="dense"
                            id="name"
                            label="Puanınızı Girin"
                            type="number"
                            value={inputGrade}
                            fullWidth
                            onChange={handleVoteChange}
                            helperText="Girilen değer 0 ile 100 arasında olmalıdır."
                            className={classes.voteTextField}
                        />
                    </DialogContent>

                    <DialogActions>
                        <Button disabled={!isValidInput} onClick={handleOkButton} variant="contained" className={classes.okButton} >
                            KAYDET
                    </Button>
                        <Button onClick={handleClose} variant="contained" className={classes.cancelButton}>
                            İPTAL
                    </Button>
                    </DialogActions>

                </div>
            </Dialog>
        </div >
    );
}
