import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Slide, Paper, ButtonGroup } from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import ClearIcon from '@material-ui/icons/Clear';

import SetObjectivesAddObjForm from "./setObjectivesAddObjForm"
import SetObjectivesSelectEmployee from "./setObjectivesSelectEmployee"

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide(props) {

    console.log("isOpen on dialog:::: ", props.open)

    const handleClickOpen = () => {
        //setOpen(true);
    };

    const handleClose = (evet, reason) => {
        if(reason === 'backdropClick'){
            return;
        }
        props.handleCloseDialog()
    };

    var newObjective = ""
    const initNewObjective = (i) => {
        console.log("initNewObjective: ", i)
        newObjective = i
    }

    const handleSaveObjectiveButton = () => {
        props.saveObjective(newObjective)
    }

    return (
        <div style={{ width: "" }}>
            <Dialog
                open={props.open}
                //TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
                maxWidth="lg"
            >
                <DialogTitle id="alert-dialog-slide-title" style={{backgroundColor: "#b71c1c", color:"white", height: ""}}>{"Hedef Ekleme"}</DialogTitle>
                <DialogContent style={{ height: "" }}>
                    <DialogContentText id="alert-dialog-slide-description">
                    
                    {props.employee === "" & !props.isUserAsEmployee ? <h5>Hedef girilecek Personel Seçin!</h5>
                    :
                    props.employee.name + " " + props.employee.surname
                    }
                        
                    </DialogContentText>

                    <SetObjectivesAddObjForm employee={props.employee} saveObjective={props.saveObjective} initNewObjective={initNewObjective} />


                </DialogContent>

                <DialogActions>

                    <ButtonGroup variant="text" size="large" color="primary" aria-label="text primary button group">
                        <Button
                            startIcon={<SaveIcon />}
                            style={{ color: "grey" }}
                            onClick={handleSaveObjectiveButton}
                        >
                            Kaydet
                                </Button>
                        <Button
                            startIcon={<ClearIcon />}
                            style={{ color: "grey" }}
                            onClick={handleClose}>
                            İptal
                                        </Button>
                        {/* <Button>Three</Button> */}
                    </ButtonGroup>

                </DialogActions>
            </Dialog>
        </div>
    );
}
